import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components';
import Pro from './Pro/Pro.js';
import Works from './Works/Works.js';
import { Element, Link, scrollSpy, Events } from 'react-scroll';

import {
  BrowserRouter as Router,
  Route,
  NavLink
} from 'react-router-dom';

import About from './About/About.js';

const Header = styled.div`
    text-align: center;
    position: fixed;
    width: 100vw;
    background-color: white;
    padding: 20px;
    z-index: 1;
`;

const LinkContainer = styled.div`
    width: 300px;
    display: inline-block;

    a {
        margin: 10px;
        font-size: 18px;
        color: #4A4948;
        text-decoration: none;
    }

    .active {
        color: #9B9B9B;
    }
`;

class App extends Component {
    render() {
        return (
            <Router>
                <div className="App">
                    <Header>
                        <LinkContainer>
                            <Link activeClass="active" to="about" spy={true} smooth={true} duration={500}>
                                About
                            </Link>
                            <Link activeClass="active" to="professions" spy={true} smooth={true} duration={500}>
                                Professions
                            </Link>
                            <Link activeClass="active" to="works" spy={true} smooth={true} duration={500}>
                                Works
                            </Link>
                        </LinkContainer>
                    </Header>

                    <Element name="about">
                        <About ref="about"></About>
                    </Element>
                    <Element name="professions">
                        <Pro ref="pro"></Pro>
                    </Element>
                    <Element name="works">
                        <Works ref="works"></Works>
                    </Element>
                </div>
            </Router>
        );
    }
}

export default App;



